import React from 'react';
import { Link } from 'react-router-dom';
import { 
  Card,
  CardContent,
  Typography,
  Box,
  Chip,
  useTheme
} from '@mui/material';
import dayjs from 'dayjs';
import 'dayjs/locale/ko';

const NewsItem = ({ item }) => {
  const theme = useTheme();
  
  // item이 존재하지 않으면 빈 컴포넌트 반환
  if (!item) {
    return null;
  }
  
  const formatDate = (date) => {
    if (!date) return '';
    return dayjs(date).locale('ko').format('YYYY.MM.DD');
  };

  // topics가 존재하고 배열인지 확인
  const hasTopics = Array.isArray(item.topics) && item.topics.length > 0;
  
  // 안전하게 태그 추출 (최대 3개)
  const displayTopics = hasTopics 
    ? item.topics.slice(0, 3).map((topic, idx) => 
        typeof topic === 'string' ? topic : (topic?.topic || ''))
    : [];

  return (
    <Link 
      to={`/news/${item.id}`} 
      style={{ textDecoration: 'none' }}
    >
      <Card 
        sx={{
          height: '100%',
          display: 'flex',
          flexDirection: 'column',
          transition: 'transform 0.2s, box-shadow 0.2s',
          '&:hover': {
            transform: 'translateY(-4px)',
            boxShadow: `0 5px 15px rgba(0,0,0,0.1)`,
            '& .news-title': {
              color: theme.palette.primary.main
            }
          },
          borderRadius: 2,
          bgcolor: 'background.paper'
        }}
      >
        <CardContent sx={{ flexGrow: 1, p: 2.5 }}>
          <Box sx={{ mb: 1, display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
            <Chip 
              label={item.source || 'Unknown'} 
              size="small" 
              variant="outlined" 
              sx={{ 
                borderRadius: 1, 
                fontSize: '0.75rem', 
                height: 24 
              }} 
            />
            <Typography 
              variant="caption" 
              color="text.secondary"
              sx={{ fontSize: '0.75rem' }}
            >
              {formatDate(item.published_date)}
            </Typography>
          </Box>

          <Typography 
            variant="h6" 
            component="h2" 
            gutterBottom 
            className="news-title"
            sx={{ 
              fontSize: '1rem', 
              fontWeight: 600, 
              lineHeight: 1.4,
              mb: 1.5,
              overflow: 'hidden',
              textOverflow: 'ellipsis',
              display: '-webkit-box',
              WebkitLineClamp: 2,
              WebkitBoxOrient: 'vertical'
            }}
          >
            {item.title || 'No Title'}
          </Typography>

          {item.summary && (
            <Typography 
              variant="body2" 
              color="text.secondary"
              sx={{
                overflow: 'hidden',
                textOverflow: 'ellipsis',
                display: '-webkit-box',
                WebkitLineClamp: 3,
                WebkitBoxOrient: 'vertical',
                lineHeight: 1.6,
                mb: 1.5
              }}
            >
              {item.summary}
            </Typography>
          )}

          {displayTopics.length > 0 && (
            <Box sx={{ mt: 'auto', display: 'flex', flexWrap: 'wrap', gap: 0.5 }}>
              {displayTopics.map((topic, i) => (
                topic && <Chip 
                  key={i} 
                  label={topic} 
                  size="small"
                  sx={{ 
                    borderRadius: 1, 
                    fontSize: '0.7rem', 
                    height: 20,
                    bgcolor: 'rgba(144, 202, 249, 0.1)',
                    color: 'primary.light',
                    '& .MuiChip-label': {
                      px: 1
                    }
                  }}
                />
              ))}
            </Box>
          )}
        </CardContent>
      </Card>
    </Link>
  );
};

export default NewsItem;
