import React, { useState, useEffect } from 'react';
import { useParams, Link } from 'react-router-dom';
import { 
  Box, 
  Typography, 
  Paper, 
  Chip, 
  Button, 
  Divider, 
  Card,
  CardContent,
  useMediaQuery,
  Skeleton,
  Alert
} from '@mui/material';
import { useTheme } from '@mui/material/styles';
import OpenInNewIcon from '@mui/icons-material/OpenInNew';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import dayjs from 'dayjs';
import 'dayjs/locale/ko';
import { getNewsById } from '../services/newsService';

const NewsDetailPage = () => {
  const { id } = useParams();
  const [news, setNews] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));

  useEffect(() => {
    const fetchNewsDetail = async () => {
      try {
        setLoading(true);
        const newsItem = await getNewsById(id);
        if (newsItem) {
          setNews(newsItem);
        } else {
          setError('뉴스를 찾을 수 없습니다');
        }
      } catch (err) {
        console.error('뉴스 상세 정보 로딩 실패:', err);
        setError('뉴스 정보를 불러오는 데 실패했습니다');
      } finally {
        setLoading(false);
      }
    };

    fetchNewsDetail();
  }, [id]);

  const formatDate = (date) => {
    if (!date) return '';
    return dayjs(date).locale('ko').format('YYYY년 MM월 DD일 HH:mm');
  };

  // 안전한 토픽 처리
  const safeTopics = React.useMemo(() => {
    if (!news || !Array.isArray(news.topics)) return [];
    return news.topics.filter(topic => topic); // null/undefined 제거
  }, [news]);

  // 안전한 컨텐츠 처리
  const safeContent = React.useMemo(() => {
    if (!news || !Array.isArray(news.content)) return [];
    return news.content.filter(paragraph => paragraph); // null/undefined 제거
  }, [news]);

  if (loading) {
    return (
      <Box sx={{ mt: 4 }}>
        <Skeleton variant="text" height={40} width="80%" sx={{ mb: 1 }} />
        <Skeleton variant="text" height={20} width="20%" sx={{ mb: 2 }} />
        <Skeleton variant="rectangular" height={250} sx={{ mb: 3 }} />
        <Skeleton variant="text" height={20} width="90%" />
        <Skeleton variant="text" height={20} width="90%" />
        <Skeleton variant="text" height={20} width="90%" />
        <Skeleton variant="text" height={20} width="70%" />
      </Box>
    );
  }

  if (error) {
    return (
      <Box sx={{ mt: 4 }}>
        <Alert severity="error" sx={{ mb: 2 }}>{error}</Alert>
        <Button 
          component={Link} 
          to="/" 
          startIcon={<ArrowBackIcon />} 
          variant="outlined"
        >
          뉴스 목록으로 돌아가기
        </Button>
      </Box>
    );
  }

  if (!news) return null;

  return (
    <Box sx={{ mt: 3, mb: 6 }}>
      <Button 
        component={Link}
        to="/"
        startIcon={<ArrowBackIcon />}
        sx={{ mb: 2 }}
        variant="text"
      >
        뉴스 목록으로
      </Button>

      <Paper 
        elevation={0}
        sx={{ 
          p: isMobile ? 2 : 4, 
          borderRadius: 2,
          bgcolor: 'background.paper',
          mb: 3
        }}
      >
        <Typography 
          variant="h4" 
          component="h1" 
          gutterBottom
          sx={{
            fontSize: isMobile ? '1.5rem' : '2rem',
            fontWeight: 600,
            lineHeight: 1.3
          }}
        >
          {news.title}
        </Typography>

        <Box sx={{ 
          display: 'flex', 
          justifyContent: 'space-between', 
          alignItems: 'center', 
          mb: 3, 
          flexWrap: 'wrap',
          gap: 1
        }}>
          <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
            <Chip 
              label={news.source} 
              size="small" 
              color="primary" 
              variant="outlined"
              sx={{ borderRadius: 1 }}
            />
            <Typography variant="body2" color="text.secondary">
              {formatDate(news.published_date)}
            </Typography>
          </Box>
          <Button 
            variant="contained" 
            size="small" 
            endIcon={<OpenInNewIcon />}
            onClick={() => window.open(news.link, '_blank')}
            sx={{
              borderRadius: 1.5,
              textTransform: 'none',
              fontWeight: 500
            }}
          >
            원문 보기
          </Button>
        </Box>

        {news.summary && (
          <>
            <Typography variant="h6" gutterBottom sx={{ fontWeight: 600, mt: 2 }}>
              요약
            </Typography>
            <Typography variant="body1" paragraph sx={{ lineHeight: 1.8 }}>
              {news.summary}
            </Typography>
          </>
        )}
        
        <Divider sx={{ my: 2 }} />
        
        <Typography variant="h6" gutterBottom sx={{ fontWeight: 600 }}>
          주요 내용
        </Typography>
        {safeContent.length > 0 ? (
          safeContent.map((paragraph, index) => (
            <Typography 
              key={index} 
              variant="body1" 
              paragraph 
              sx={{ lineHeight: 1.8 }}
            >
              {paragraph}
            </Typography>
          ))
        ) : (
          <Typography variant="body2" color="text.secondary">
            본문 내용이 없습니다. 원문 기사를 참고하세요.
          </Typography>
        )}
      </Paper>

      {safeTopics.length > 0 && (
        <Card variant="outlined" sx={{ mb: 3, borderRadius: 2 }}>
          <CardContent>
            <Typography variant="h6" gutterBottom sx={{ fontWeight: 600 }}>
              관련 토픽
            </Typography>
            <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 1 }}>
              {safeTopics.map((topic, index) => (
                <Chip 
                  key={index} 
                  label={typeof topic === 'string' ? topic : (topic?.topic || '기타')} 
                  size="small" 
                  sx={{ borderRadius: 1 }}
                />
              ))}
            </Box>
          </CardContent>
        </Card>
      )}
      
      <Box sx={{ display: 'flex', justifyContent: 'center', mt: 4 }}>
        <Button 
          variant="outlined" 
          size="large" 
          endIcon={<OpenInNewIcon />}
          onClick={() => window.open(news.link, '_blank')}
          sx={{
            borderRadius: 2,
            px: 3,
            py: 1,
            textTransform: 'none',
            fontWeight: 500
          }}
        >
          원문 기사 읽기
        </Button>
      </Box>
    </Box>
  );
};

export default NewsDetailPage;
