import React, { useState } from 'react';
import { Link } from 'react-router-dom';

const TermList = ({ terms }) => {
  const [filter, setFilter] = useState('');
  const [selectedCategory, setSelectedCategory] = useState('');
  
  // 카테고리 추출 (중복 제거)
  const categories = [...new Set(terms.map(term => term.category))];
  
  // 필터링 적용
  const filteredTerms = terms.filter(term => {
    const matchesSearch = term.term.toLowerCase().includes(filter.toLowerCase()) ||
                         term.definition.toLowerCase().includes(filter.toLowerCase()) ||
                         (term.summary && term.summary.toLowerCase().includes(filter.toLowerCase()));
    const matchesCategory = selectedCategory === '' || term.category === selectedCategory;
    
    return matchesSearch && matchesCategory;
  });

  return (
    <div className="term-list">
      <div className="term-filters">
        <input
          type="text"
          placeholder="용어 검색..."
          value={filter}
          onChange={(e) => setFilter(e.target.value)}
          className="search-input"
        />
        
        <select 
          value={selectedCategory} 
          onChange={(e) => setSelectedCategory(e.target.value)}
          className="category-select"
        >
          <option value="">모든 카테고리</option>
          {categories.map(category => (
            <option key={category} value={category}>{category}</option>
          ))}
        </select>
      </div>
      
      <div className="terms-container">
        {filteredTerms.length === 0 ? (
          <p>검색 결과가 없습니다.</p>
        ) : (
          filteredTerms.map(term => (
            <div key={term.id} className="term-card">
              <h3>{term.term}</h3>
              <p className="term-category">{term.category}</p>
              {term.summary && <p className="term-summary">{term.summary}</p>}
              <p className="term-definition">{term.definition}</p>
              <Link 
                to={`/terms/${term.id}`}
                className="term-details-link"
                onClick={() => console.log("Navigating to term ID:", term.id)}
              >
                자세히 보기
              </Link>
            </div>
          ))
        )}
      </div>
    </div>
  );
};

export default TermList;
