import economicTerms from '../data/economicTerms';

const getAllTerms = () => {
  return economicTerms;
};

const getTermById = (id) => {
  // 문자열로 전달된 id를 숫자로 변환하여 비교
  const numericId = parseInt(id, 10);
  if (isNaN(numericId)) {
    console.error("Invalid ID format:", id);
    return null;
  }
  
  const term = economicTerms.find(term => term.id === numericId);
  if (!term) {
    console.warn(`Term with ID ${id} not found`);
  }
  return term;
};

const getTermsByCategory = (category) => {
  return economicTerms.filter(term => term.category === category);
};

const searchTerms = (query) => {
  const lowerQuery = query.toLowerCase();
  return economicTerms.filter(term => 
    term.term.toLowerCase().includes(lowerQuery) || 
    term.definition.toLowerCase().includes(lowerQuery)
  );
};

export { getAllTerms, getTermById, getTermsByCategory, searchTerms };
