import React, { useEffect } from 'react';
import { useParams, Link } from 'react-router-dom';
import { getTermById } from '../services/termService';

const TermDetail = () => {
  const { id } = useParams();
  
  // 디버깅용 로그
  useEffect(() => {
    console.log("Term ID from URL:", id);
  }, [id]);
  
  const term = getTermById(id);
  
  // 디버깅용 로그
  useEffect(() => {
    console.log("Retrieved term:", term);
  }, [term]);

  if (!term) {
    return (
      <div className="term-not-found">
        <h2>용어를 찾을 수 없습니다.</h2>
        <p>ID: {id}</p>
        <Link to="/terms" className="back-link">← 목록으로 돌아가기</Link>
      </div>
    );
  }

  return (
    <div className="term-detail">
      <Link to="/terms" className="back-link">← 목록으로 돌아가기</Link>
      
      <div className="term-header">
        <h1>{term.term}</h1>
        <span className="term-category">{term.category}</span>
      </div>
      
      <div className="term-content">
        {term.summary && (
          <>
            <h2>요약</h2>
            <p className="term-summary-text">{term.summary}</p>
          </>
        )}
        
        <h2>정의</h2>
        <p>{term.definition}</p>
        
        {term.examples && term.examples.length > 0 && (
          <>
            <h2>예시</h2>
            <ul className="examples-list">
              {term.examples.map((example, index) => (
                <li key={index}>{example}</li>
              ))}
            </ul>
          </>
        )}
      </div>
    </div>
  );
};

export default TermDetail;
